@Composable
fun BGRubber() {
    Column(modifier = Modifier
        .margin(top = 60.px)
        .fillMaxWidth()
    ) {
        ApplicationTitle(
            modifier = Modifier.fillMaxWidth(),
            title = "BG Rubber"
        )

        TechStackTitle(
            modifier = Modifier.fillMaxWidth().margin(topBottom = 5.px),
            title = "Technologies: OpenCV, SnapKit, Combine, CoreML"
        )

        BGRubberDescription()
        BGRubberAppGuides()
        BGRubberScrollBars()

        Column(
            modifier = Modifier
                .fillMaxWidth()
                .margin(top = 50.px),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            LinkButton("https://apps.apple.com/gb/app/bg-rubber-background-remover/id6444268627?mt=12", "macOS")
        }
    }
}

@Composable()
fun BGRubberDescription() {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .margin(leftRight = 0.px)
    ) {
        ExperienceDescriptionLabel("- Auto removal of background image using CoreML")
        ExperienceDescriptionLabel("- Zoom In/Out and background removal/restoration features using Gesture Recognizer")
        ExperienceDescriptionLabel("- Completed image saving feature")
    }
}

@Composable
fun BGRubberAppGuides() {
    Row (
        modifier = Modifier
            .fillMaxWidth()
            .id("bgRubberContainer")
            .maxWidth(1400.px)
            .margin(leftRight = 0.px, top = 20.px, bottom = 30.px)
            .overflow(Overflow.Hidden)
            .scrollBehavior(ScrollBehavior.Smooth)
    ) {
        Image(
            src = "bgRubberPreview.gif",
            modifier = Modifier
                .width(480.px)
                .height(270.px)
                .margin(right = 20.px)
                .objectFit(ObjectFit.Contain)
        )
        Image(
            src = "bgRubber1.png",
            modifier = Modifier
                .width(480.px)
                .height(270.px)
                .margin(right = 20.px)
                .objectFit(ObjectFit.Contain)
        )
        Image(
            src = "bgRubber2.png",
            modifier = Modifier
                .width(480.px)
                .height(270.px)
                .margin(right = 20.px)
                .objectFit(ObjectFit.Contain)
        )
        Image(
            src = "bgRubber3.png",
            modifier = Modifier
                .width(480.px)
                .height(270.px)
                .margin(right = 20.px)
                .objectFit(ObjectFit.Contain)
        )
        Image(
            src = "bgRubber4.png",
            modifier = Modifier
                .width(480.px)
                .height(270.px)
                .margin(right = 20.px)
                .objectFit(ObjectFit.Contain)
        )
    }
}

@Composable
fun BGRubberScrollBars() {
    Row(
        modifier = Modifier.fillMaxWidth(),
        horizontalArrangement = Arrangement.Center
    ) {
        FaArrowLeft(
            modifier = PortfolioArrowIconStyle.toModifier()
                .margin(right = 40.px)
                .cursor(Cursor.Pointer)
                .onClick {
                    document.getElementById("bgRubberContainer")
                        ?.scrollBy(x = (-450.0), y = 0.0)
                },
            size = IconSize.LG
        )
        FaArrowRight(
            modifier = PortfolioArrowIconStyle.toModifier()
                .cursor(Cursor.Pointer)
                .onClick {
                    document.getElementById("bgRubberContainer")
                        ?.scrollBy(x = 450.0, y = 0.0)
                },
            size = IconSize.LG
        )
    }
}

val PortfolioArrowIconStyle by ComponentStyle {
    base {
        Modifier
            .color(Theme.Gray.rgb)
            .transition(CSSTransition(property = "color", duration = 200.ms))
    }
    hover {
        Modifier.color(Theme.Primary.rgb)
    }
}