package com.arrrpark.portfoilo.pages

import androidx.compose.runtime.Composable
import com.arrrpark.portfoilo.models.Section
import com.arrrpark.portfoilo.models.Theme
import com.arrrpark.portfoilo.sections.*
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.ObjectFit
import com.varabyte.kobweb.compose.css.Overflow
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.graphics.Image
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Page
@Composable
fun HomePage() {
    Column (
        modifier = Modifier
            .fillMaxSize(),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        NavHeader()
        MainBackground()
        Column(
            modifier = Modifier
                .id(Section.About.id)
                .fillMaxWidth(70.percent)
        ) {
            AboutSection()
        }

        Column(
            modifier = Modifier
                .fillMaxWidth()
                .id(Section.WorkExperience.id)
                .padding(topBottom = 50.px)
                .backgroundColor(Theme.LighterGray.rgb),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Column(
                modifier = Modifier.fillMaxWidth(70.percent)
            ) {
                ExperienceSection()
            }
        }

        Column(
            modifier = Modifier
                .id(Section.Projects.id)
                .fillMaxWidth(60.percent)
                .padding(top = 50.px)
        ) {
            ProjectSection()
        }

        Column(
            modifier = Modifier
                .id(Section.PersonalProjects.id)
                .fillMaxWidth()
                .padding(topBottom = 50.px)
                .backgroundColor(Theme.LighterGray.rgb),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Column(
                modifier = Modifier.fillMaxWidth(60.percent)
            ) {
                PersonalProjectSection()
            }
        }

        Column(
            modifier = Modifier
                .id(Section.Education.id)
                .fillMaxWidth(60.percent)
        ) {
            EducationSection()
        }
    }
}

@Composable
fun MainBackground() {
    Box(
        modifier = Modifier.fillMaxSize()
    ) {
        Image(
            modifier = Modifier
                .fillMaxWidth()
                .height(920.px)
                .objectFit(ObjectFit.Cover)
                .overflow(Overflow.Hidden),
            src = "background.jpg"
        )
        Column(
            modifier = Modifier
                .fillMaxSize(),
            horizontalAlignment = Alignment.CenterHorizontally,
            verticalArrangement = Arrangement.Top
        ) {
            P(
                attrs = Modifier
                    .fontWeight(FontWeight.Bold)
                    .fontSize(55.px)
                    .margin(top = 230.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("Hello, I'm Seungmyun Park")
            }

            P(
                attrs = Modifier
                    .fontWeight(FontWeight.Normal)
                    .fontSize(25.px)
                    .margin(top = 20.px, bottom = 5.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("I'm a mobile app developer focusing on iOS development,")
            }
            P(
                attrs = Modifier
                    .fontWeight(FontWeight.Normal)
                    .fontSize(25.px)
                    .margin(top = 0.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("who is also capable of react-native and android development")
            }
        }
    }
}
