@Composable
fun EducationSection() {
    Column (
        modifier = Modifier
            .fillMaxWidth()
            .margin(bottom = 20.px)
    ) {
        SectionTitle(
            modifier = Modifier
                .fillMaxWidth()
                .margin(top = 60.px, bottom = 20.px),
            section = Section.Education,
            alignment = Alignment.CenterHorizontally
        )

        Row(
            modifier = Modifier.margin(top = 30.px, bottom = 100.px)
        ) {
            P(
                attrs = Modifier
                    .width(300.px)
                    .margin(topBottom = 0.px)
                    .fontSize(20.px)
                    .fontWeight(FontWeight.Bold)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("2011 - 2017")
            }
            Column {
                P(
                    attrs = Modifier
                        .margin(topBottom = 0.px)
                        .fontSize(20.px)
                        .fontWeight(FontWeight.Normal)
                        .color(Theme.Secondary.rgb)
                        .toAttrs()
                ) {
                    Text("Bachelor of information System, Hanyang University, Seoul, South Korea")
                }

                P(
                    attrs = Modifier
                        .margin(top = 5.px)
                        .fontSize(20.px)
                        .fontWeight(FontWeight.Bold)
                        .color(Theme.Secondary.rgb)
                        .toAttrs()
                ) {
                    Text("GPA: 3.87 / 4.5")
                }

                Column {
                    P(
                        attrs = Modifier
                            .fillMaxWidth()
                            .margin(top = 5.px)
                            .fontSize(16.px)
                            .fontWeight(FontWeight.Normal)
                            .color(Theme.Secondary.rgb)
                            .toAttrs()
                    ) {
                        Text("Courses: C++, Java, Data structure, Database system, Operating System, System planning, etc")
                    }
                }
