@Composable
fun AutoCaptureIOS() {
    Column(modifier = Modifier.fillMaxWidth()) {
        ApplicationTitle(
            modifier = Modifier
                .fillMaxWidth()
                .margin(top = 70.px),
            title = "AutoCapture iOS, AutoCompose macOS application"
        )

        TechStackTitle(
            modifier = Modifier
                .fillMaxWidth()
                .margin(topBottom = 5.px),
            title = "Technologies: OpenCV, Canon SDK, Bluetooth, GCD web server, Firebase MLKit"
        )

        AutosIOSDescription()

        Row (
            modifier = Modifier
                .fillMaxWidth()
                .margin(top = 30.px),
            horizontalArrangement = Arrangement.Center,
            verticalAlignment = Alignment.CenterVertically
        ) {

            Column(
                modifier = Modifier,
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Image(
                    src = "cutAppCars.PNG",
                    modifier = Modifier
                        .width(300.px)
                        .margin(bottom = 20.px)
                        .objectFit(ObjectFit.Contain)
                )

                SpanText("Images captured by macOS and Canon SDK",
                    modifier = Modifier
                        .fontSize(14.px)
                        .fontWeight(FontWeight.SemiBold)
                )
            }

            FaArrowRight(
                modifier = Modifier
                    .margin(leftRight = 15.px)
            )

            Column(
                modifier = Modifier,
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Image(
                    src = "carsOnWeb.PNG",
                    modifier = Modifier
                        .width(300.px)
                        .margin(bottom = 5.px)
                        .objectFit(ObjectFit.Contain)
                )

                SpanText("Car images on Web",
                    modifier = Modifier
                        .fontSize(14.px)
                        .fontWeight(FontWeight.SemiBold)
                )
            }

            FaArrowRight(
                modifier = Modifier
                    .margin(leftRight = 15.px)
            )

            Column(
                modifier = Modifier,
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Image(
                    src = "rotatingCar.gif",
                    modifier = Modifier
                        .width(300.px)
                        .margin(bottom = 20.px)
                        .objectFit(ObjectFit.Contain)
                )

                SpanText("360 View of the car",
                    modifier = Modifier
                        .fontSize(14.px)
                        .fontWeight(FontWeight.SemiBold)
                )
            }
        }

        Row (
            modifier = Modifier
                .fillMaxWidth()
                .margin(top = 30.px),
            horizontalArrangement = Arrangement.Center
        ) {
            LinkButton("https://apps.apple.com/gb/app/aos-autocapture/id1115610872", title = "iOS")
            SpanText("", modifier = Modifier.width(50.px))
            LinkButton("https://download.aos.tv", title = "macOS")
        }
    }
}

@Composable()
fun AutosIOSDescription() {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .margin(leftRight = 0.px)
    ) {
        ExperienceDescriptionLabel("- Performance improvement, migration and maintenance of the app", fontWeight = FontWeight.Bold)
        ExperienceDescriptionLabel("- MacOS, Canon SDK, blueTooth integration work")
        ExperienceDescriptionLabel("- Migrating the app from Objective-C to Swift with the help of SwiftRewriter. About 50% of the whole code is migrated")
        ExperienceDescriptionLabel("- Added the feature that detect cars and subsequently but them while maintaining the same margin on each side. Concatenated images to make 360 view of the car")
        ExperienceDescriptionLabel("- Added the feature of adjusting photo brightness, temperature, and zoom")
    }
}

@Composable
fun OriginalCarPicture(src: String) {
    Image(
        src = src,
        modifier = Modifier
            .width(100.px)
            .objectFit(ObjectFit.Contain)
    )
}