package com.arrrpark.portfoilo.sections

import androidx.compose.runtime.Composable
import com.arrrpark.portfoilo.components.ExperienceCard
import com.arrrpark.portfoilo.components.SectionTitle
import com.arrrpark.portfoilo.models.Experience
import com.arrrpark.portfoilo.models.Section
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.padding
import org.jetbrains.compose.web.css.px

@Composable
fun ExperienceSection() {
    SectionTitle(
        modifier = Modifier
            .fillMaxWidth()
            .padding(top = 50.px),
        section = Section.WorkExperience,
        alignment = Alignment.CenterHorizontally
    )

    Column(
        modifier = Modifier
            .fillMaxWidth()
            .margin(top = 50.px)
            .padding(bottom = 100.px),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Experience.values().forEach { experience ->
            ExperienceCard(experience)
    }}
}