@Composable
fun SuperVankAndroid() {
    Column(
        modifier = Modifier
            .margin(top = 80.px)
            .fillMaxWidth(),
        verticalArrangement = Arrangement.Top
    ) {
        ApplicationTitle(
            modifier = Modifier.fillMaxWidth(),
            title = "Supervank Android application"
        )

        TechStackTitle(
            modifier = Modifier.fillMaxWidth().margin(topBottom = 5.px),
            title = "Technologies: MVC, JAVA, AD SDK, Camera SDK, Glide, Contents provider"
        )

        SuperVankAndroidDescription()
    }
    Column (
        modifier = Modifier
            .fillMaxWidth()
            .margin(top = 60.px),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        SuperVankAppGuides()
    }
}

@Composable()
fun SuperVankAndroidDescription() {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .margin(leftRight = 0.px)
    ) {
        ExperienceDescriptionLabel("- Developed a pedometer using a gyro sensor. It calculates total steps, calories burned, total distance by day/month/year", FontWeight.Bold)
        ExperienceDescriptionLabel("- Developed screen for investment results")
        ExperienceDescriptionLabel("- Implemented AD SDK from various vendors such as facebook, unity, google. Rewards are given after users finished watching the ads")
        ExperienceDescriptionLabel("- Developed custom gallery using contents provider and recyclerview")
        ExperienceDescriptionLabel("- Developed custom camera that is able to zoom-in, zoom-out, cropping using Camera api")
        P(
            attrs = Modifier
                .margin(top = 0.px, bottom = 3.px, leftRight = 0.px)
                .fontSize(17.px)
                .fontWeight(FontWeight.Normal)
                .color(Color.red)
                .toAttrs()
        ) {
            Text("- Due to the company's policy, this app is currently down from the play store")
        }
    }
}


