import com.arrrpark.portfoilo.Projects.AutoCaptureIOS
import com.arrrpark.portfoilo.Projects.SticpayRN
import com.arrrpark.portfoilo.Projects.SuperVankAndroid
import com.arrrpark.portfoilo.components.SectionTitle
import com.arrrpark.portfoilo.models.Section
import com.arrrpark.portfoilo.models.Theme
import com.varabyte.kobweb.compose.css.CSSTransition
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.css.TextDecorationLine
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.navigation.OpenLinkStrategy
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.anyLink
import com.varabyte.kobweb.silk.components.style.hover
import com.varabyte.kobweb.silk.components.style.toModifier
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.px
import public.SticpayIOS

val NavigationItemStyle by ComponentStyle {
    base {
        Modifier
            .color(Theme.Secondary.rgb)
            .transition(CSSTransition(property = "color", duration = 200.ms))
    }
    anyLink {
        Modifier.color(Theme.Secondary.rgb)
    }
    hover {
        Modifier.color(Theme.Primary.rgb)
    }
}

@Composable
fun ProjectSection() {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .padding(top = 50.px, bottom = 150.px)
    ) {
        SectionTitle(
            modifier = Modifier
                .fillMaxWidth(),
            section = Section.Projects,
            alignment = Alignment.CenterHorizontally
        )

        AutoCaptureIOS()
        SticpayIOS()
        SticpayRN()
        SuperVankAndroid()
    }
}

@Composable
fun LinkButton(path: String, title: String = "Project Link") {
    Link(
        path = path,
        text = title,
        modifier = NavigationItemStyle.toModifier()
            .minWidth(200.px)
            .padding(leftRight = 20.px, topBottom = 15.px)
            .color(Color.white)
            .textAlign(textAlign = TextAlign.Center)
            .fontSize(20.px)
            .fontWeight(FontWeight.Bold)
            .textDecorationLine(TextDecorationLine.None)
            .borderRadius(10.px)
            .backgroundColor(Theme.MarinBlue.rgb),
        openExternalLinksStrategy = OpenLinkStrategy.IN_NEW_TAB
    )
}