@Composable
fun AboutSection() {
    SectionTitle(
        modifier = Modifier
            .fillMaxWidth()
            .margin(top = 100.px),
        section = Section.About,
        alignment = Alignment.CenterHorizontally
    )

    AboutGrid()
}

@Composable()
fun AboutGrid() {
    Row (
        modifier = Modifier
            .fillMaxWidth()
            .margin(top = 50.px, bottom = 150.px),
        horizontalArrangement = Arrangement.SpaceBetween
    ) {
        Column(
            modifier = Modifier
                .margin(left = 50.px)
                .width(600.px)
                .fillMaxHeight()
        ) {
            P(
                attrs = Modifier
                    .margin(bottom = 15.px)
                    .fontWeight(FontWeight.Bold)
                    .lineHeight(1.5)
                    .fontSize(25.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                SpanText("Get to know me")
            }

            P(
                attrs = Modifier
                    .margin(bottom = 15.px)
                    .fontWeight(FontWeight.Normal)
                    .lineHeight(1.5)
                    .fontSize(18.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("I am ")
                SpanText("a skilled mobile client engineer with around 5 years of industry experience.", modifier = Modifier.fontWeight(FontWeight.Bold))
                Text("I have a verifiable history of contributing directly to company growth and expansion throughout my career.")
            }

            P(
                attrs = Modifier
                    .margin(topBottom = 15.px)
                    .fontWeight(FontWeight.Normal)
                    .lineHeight(1.5)
                    .fontSize(18.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("I have consistently exceeded performance goals and I am adept at achieving maximum operational output with minimal resource expenditure. ")
                SpanText("My current focus is on iOS development, but I have experience in android and react-native as well.", modifier = Modifier.fontWeight(FontWeight.Bold))
            }

            P(
                attrs = Modifier
                    .margin(topBottom = 15.px)
                    .fontWeight(FontWeight.Normal)
                    .lineHeight(1.5)
                    .fontSize(18.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text("I am also highly interested in technologies that help me to make maintainable and elegant mobile application, such as MVVM pattern, reactive programming and unit test.")
            }
        }

        Column(
            modifier = Modifier
                .width(500.px)
                .fillMaxHeight().color(Color.green)
        ) {
            P(
                attrs = Modifier
                    .margin(bottom = 15.px)
                    .fontWeight(FontWeight.Bold)
                    .lineHeight(1.5)
                    .fontSize(25.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                SpanText("Proficient")
            }

            Row(
                modifier = Modifier
                    .fillMaxWidth(),
            ) {
                SkillCard("iOS")
                SkillCard("Swift")
                SkillCard("Objective-C")
            }

            P(
                attrs = Modifier
                    .margin(top = 20.px, bottom = 15.px)
                    .fontWeight(FontWeight.Bold)
                    .lineHeight(1.5)
                    .fontSize(25.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                SpanText("Intermediate")
            }

            Row(
                modifier = Modifier
                    .fillMaxWidth()
            ) {
                SkillCard("React-native")
                SkillCard("Flow")
                SkillCard("ES6")
                SkillCard("macOS")
            }

            Row(
                modifier = Modifier
                    .margin(top = 10.px)
                    .fillMaxWidth()
            ) {
                SkillCard("Android")
                SkillCard("Kotlin")
                SkillCard("Java")
                SkillCard("OpenCV")
            }

            P(
                attrs = Modifier
                    .margin(top = 20.px, bottom = 15.px)
                    .fontWeight(FontWeight.Bold)
                    .lineHeight(1.5)
                    .fontSize(25.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                SpanText("Experienced")
            }


            Row(
                modifier = Modifier
                    .margin(top = 0.px)
                    .fillMaxWidth()
            ) {
                SkillCard("Kobweb(KMM for web)")
                SkillCard("C++")
                SkillCard("React")
            }
        }
    }
}

@Composable
fun SkillCard(name: String) {
    Row{
        Row(
            modifier = Modifier
                .backgroundColor(Theme.LighterGray.rgb)
                .borderRadius(10.px)
        ) {
            P(
                attrs = Modifier
                    .margin(topBottom = 15.px, leftRight = 20.px)
                    .minWidth(40.px)
                    .textAlign(TextAlign.Center)
                    .fontWeight(FontWeight.SemiBold)
                    .fontSize(15.px)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                SpanText(name)
            }
        }

        SpanText("", modifier = Modifier.width(15.px))
    }
}