@Composable
fun ScanToDoc() {
    Column(modifier = Modifier.fillMaxWidth()) {
        ApplicationTitle(
            modifier = Modifier
                .fillMaxWidth()
                .margin(top = 70.px),
            title = "Scan to doc iOS application"
        )

        TechStackTitle(
            modifier = Modifier
                .fillMaxWidth()
                .margin(topBottom = 5.px),
            title = "Technologies: OpenCV, Objective-C/C++, RxSwift, SnapKit"
        )

        ScanToDocDescription()
        Row  (
            modifier = Modifier
                .margin(top = 40.px)
                .fillMaxWidth(),
            horizontalArrangement = Arrangement.SpaceEvenly,
            verticalAlignment = Alignment.CenterVertically
        ) {
            ScanToDocAppGuides()
            Column {
                LinkButton("https://apps.apple.com/gb/app/scan-to-doc/id1542458261", title = "iOS")
            }
        }
    }
}

@Composable()
fun ScanToDocDescription() {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .margin(leftRight = 0.px)
    ) {
        ExperienceDescriptionLabel("- Created iOS application, which enables users to scan and share photos materials using RxSwift, RxCocoa, OpenCV, Objective-C/C++, and Firebase")
        ExperienceDescriptionLabel("- Generated drawing feature using PencilKit, sharpening and brightening feature using OpenCV)")
        ExperienceDescriptionLabel("- Added AVFoundation to capture and save materials as well as perspective transformation to unfold scanned areas of materials")
        ExperienceDescriptionLabel("- Added sharing feature via air drop, share extension, and app group")
    }
}

