package com.arrrpark.portfoilo.components

import androidx.compose.runtime.Composable
import com.arrrpark.portfoilo.models.Theme
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.toAttrs
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun ExperienceDescriptionLabel(des: String, fontWeight: FontWeight = FontWeight.Normal) {
    P(
        attrs = Modifier
            .margin(top = 0.px, bottom = 3.px, leftRight = 0.px)
            .fontSize(17.px)
            .fontWeight(fontWeight)
            .color(Theme.Secondary.rgb)
            .toAttrs()
    ) {
        Text(des)
    }
}