package com.arrrpark.portfoilo.sections

import androidx.compose.runtime.Composable
import com.arrrpark.portfoilo.models.Section
import com.arrrpark.portfoilo.models.Theme
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.toModifier
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.Position
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px

val NavHeaderStyle by ComponentStyle {
    base {
        Modifier
            .fillMaxWidth()
            .position(Position.Sticky)
            .top(0.percent)
    }
}

@Composable
fun NavHeader() {
    Box(NavHeaderStyle.toModifier()) {
        Row(
            modifier = Modifier
                .fillMaxSize()
                .backgroundColor(Color.white)
                .height(80.px),
            horizontalArrangement = Arrangement.End,
            verticalAlignment = Alignment.CenterVertically
        ) {
            Section.values().forEach { section ->
                Link(
                    modifier = Modifier
                        .margin(leftRight = 25.px)
                        .fontWeight(FontWeight.Medium)
                        .fontSize(16.px)
                        .color(Theme.Secondary.rgb),
                    path = section.path,
                    text = section.title
                )
            }
        }
    }
}