package com.arrrpark.portfoilo.components

import androidx.compose.runtime.Composable
import com.arrrpark.portfoilo.models.Section
import com.arrrpark.portfoilo.models.Theme
import com.varabyte.kobweb.compose.css.CSSTransition
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun SectionTitle(
    modifier: Modifier = Modifier,
    section: Section,
    alignment: Alignment.Horizontal = Alignment.Start
) {
    Column(
        modifier = modifier,
        horizontalAlignment = alignment
    ) {
        P(
            attrs = Modifier
                .fillMaxWidth()
                .textAlign(
                    when (alignment) {
                        Alignment.CenterHorizontally -> TextAlign.Center
                        Alignment.End -> TextAlign.End
                        else -> TextAlign.Start
                    }
                )
                .letterSpacing(3.px)
                .margin(top = 30.px, bottom = 10.px)
                .fontSize(40.px)
                .fontWeight(FontWeight.Bold)
                .color(Theme.Primary.rgb)
                .transition(CSSTransition(property = "margin", duration = 300.ms))
                .toAttrs()
        ) {
            Text(section.title)
        }

        Box(
            modifier = Modifier
                .height(2.px)
                .width(80.px)
                .margin(top = 0.px)
                .backgroundColor(Theme.Primary.rgb)
                .borderRadius(r = 50.px)
        )

        section.subTitle?.let {
            P(
                attrs = Modifier
                    .fontSize(20.px)
                    .margin(top = 20.px, bottom = 0.px)
                    .textAlign(
                        when (alignment) {
                            Alignment.CenterHorizontally -> TextAlign.Center
                            Alignment.End -> TextAlign.End
                            else -> TextAlign.Start
                        }
                    )
                    .fontWeight(FontWeight.Normal)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text(it)
            }
        }
    }
}