package com.arrrpark.portfoilo.components

import androidx.compose.runtime.Composable
import com.arrrpark.portfoilo.models.Experience
import com.arrrpark.portfoilo.models.Theme
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.ObjectFit
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.graphics.Image
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun ExperienceCard(experience: Experience) {
    Row(
        modifier = Modifier
            .fillMaxWidth()
            .maxWidth(1200.px)
            .margin(topBottom = 20.px),
        horizontalArrangement = Arrangement.Center,
        verticalAlignment = Alignment.CenterVertically
    ) {
        Box(
            modifier = Modifier.width(100.px)
        ) {
            Image(
                src = experience.src,
                modifier = Modifier
                    .size(80.px)
                    .objectFit(ObjectFit.Contain)
            )
        }

        Column(
            modifier = Modifier
                .width(450.px)
                .fillMaxHeight()
        ) {
            P(
                attrs = Modifier
                    .margin(topBottom = 0.px, leftRight = 20.px)
                    .fontSize(15.px)
                    .fontWeight(FontWeight.Bold)
                    .color(Theme.Secondary.rgb)
                    .toAttrs()
            ) {
                Text(experience.companyAndPosition)
            }

            experience.jobList.forEach { job ->
                P(
                    attrs = Modifier
                        .width(500.px)
                        .margin(topBottom = 0.px, leftRight = 20.px)
                        .fontSize(14.px)
                        .fontWeight(FontWeight.Normal)
                        .color(Theme.Secondary.rgb)
                        .toAttrs()
                ) {
                    Text(job)
                }
            }
        }

        P(
            attrs = Modifier
                .width(400.px)
                .margin(top = 0.px)
                .fontSize(16.px)
                .fontWeight(FontWeight.Bold)
                .color(Theme.Secondary.rgb)
                .toAttrs()
        ) {
            Text(experience.period)
        }
    }
}